.col {
  float: left;
  width: 50%;
}

header {
  text-align: center;
  height: 100px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

header:before, header:after {
  content: " ";
  display: table;
}

header:after {
  clear: both;
}

header h1 {
  -webkit-app-region: drag;
  font-size: 13px;
  font-weight: normal;
  line-height: 24px;
  padding-bottom: 20px;
}

header .tab {
  float: left;
  width: 50%;
  text-align: left;
}

header .tab div {
  height: 48px;
}

