header .tab div h2 {
  font-size: 18px;
  font-weight: 400;
  padding-bottom: 6px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

header .tab div a {
  font-size: 12px;
  font-weight: 200;
  text-decoration: none;
  color: rgba(255, 255, 255, 0.6);
  transition: color .15s ease-in-out;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: default;
}

header .tab div a:hover {
  color: #34C0F9;
}

main {
  position: fixed;
  top: 100px;
  left: 0;
  right: 0;
  bottom: 60px;
}

main:before, main:after {
  content: " ";
  display: table;
}

main:after {
  clear: both;
}

main .left-panel,
main .right-panel {
  width: calc(50% - 15px);
  overflow-y: scroll;
  position: absolute;
  top: 0;
  bottom: 0;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.2);
  padding: 20px 12px 20px 20px;
}

main .left-panel::-webkit-scrollbar,
main .right-panel::-webkit-scrollbar {
  width: 8px;
}

main .left-panel::-webkit-scrollbar-thumb,
main .right-panel::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 4px;
}

main .left-panel {
  left: 10px;
}

main .right-panel {
  right: 10px;
}

main ul li {
  font-size: 15px;
  line-height: 26px;
  margin-bottom: 2px;
  position: relative;
  padding: 0 32px 0 40px;
  font-weight: 300;
  cursor: default;
  transition: background-color .15s ease-in-out;
  border-radius: 13px;
}

@media (max-width: 689px) {
  main ul li {
    padding: 0 24px 0 36px;
    font-size: 11px;
    line-height: 20px;
    border-radius: 10px;
  }
}

main ul li::before {
  content: '';
  display: block;
  width: 18px;
  height: 16px;
  position: absolute;
  left: 10px;
  top: 5px;
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="18" height="15" viewBox="0 0 18 15"><path fill="#fff" fill-rule="evenodd" d="M30.1999885,6 C31.1941074,6 32,6.96050137 32,8 L32,17.1195075 C32,18.1580748 31.1916089,19 30.1999885,19 L15.8000115,19 C14.8058926,19 14,18.1590061 14,17.1195075 L14,5.24975936 C14,4.55953633 14.5382361,4 15.2035152,4 L19.4029825,4 C20.6313955,4 21.6871935,4.89754675 22.1499946,6 L30.1999885,6 Z" transform="translate(-14 -4)"/></svg>') no-repeat center top;
  opacity: .5;
  transition: opacity .15s ease-in-out;
}

@media (max-width: 689px) {
  main ul li::before {
    top: 2px;
  }
}

main ul li.file::before {
  background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="13" height="16" viewBox="0 0 13 16"><path fill="#fff" fill-rule="evenodd" d="M285,428 L294,428 C295.104569,428 296,428.895431 296,430 L296,442 C296,443.104569 295.104569,444 294,444 L285,444 C283.895431,444 283,443.104569 283,442 L283,430 C283,428.895431 283.895431,428 285,428 Z M291.849121,441 L293.969238,441 L290.681152,431.681641 L288.275391,431.681641 L285,441 L287.120117,441 L287.792969,438.791016 L291.17627,438.791016 L291.849121,441 Z M290.706543,437.140625 L288.288086,437.140625 C288.948245,435.00357 289.348144,433.664228 289.487793,433.122559 C289.521647,433.274903 289.577718,433.482258 289.656006,433.744629 C289.734294,434.007 290.08447,435.138987 290.706543,437.140625 Z" transform="translate(-283 -428)"/></svg>') no-repeat center top;
}

main ul li::after {
  content: '';
  display: block;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 3px;
  top: 3px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  opacity: 0;
  transition: opacity .15s ease-in-out;
}

@media (max-width: 689px) {
  main ul li::after {
    width: 16px;
    height: 16px;
    top: 2px;
    right: 2px;
  }
}

main ul li:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

main ul li:hover::after {
  opacity: .2;
}

main ul li div {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

