footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  padding: 15px 10px 0;
}

footer #update-all {
  display: block;
  width: 164px;
  height: 30px;
  margin-left: auto;
  background: #031320;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 4px;
  font-size: 14px;
  color: #fff;
  font-weight: 200;
  text-align: center;
  transition: all .15s ease-in-out;
}

footer #update-all[disabled] {
  color: rgba(255, 255, 255, 0.3);
}

footer #update-all:hover:not([disabled]) {
  background-color: rgba(255, 255, 255, 0.1);
}

footer #update-all:active:not([disabled]) {
  background-color: #020b12;
  box-shadow: inset 0 2px 4px #000204;
  outline: none;
}

#overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
}

#overlay .bezel {
  text-align: center;
  position: absolute;
  top: calc(50% - 120px);
  left: calc(50% - 140px);
  width: 280px;
  height: 240px;
  background: rgba(3, 19, 32, 0.9);
  border-radius: 20px;
  box-shadow: 0 8px 20px rgba(3, 19, 32, 0.75);
}

#overlay .bezel img {
  display: block;
  margin: 50px auto 35px;
}

#overlay .bezel span {
  display: block;
}

#overlay .bezel span:nth-of-type(1) {
  color: rgba(255, 255, 255, 0.75);
  font-size: 15px;
  font-weight: 300;
  padding-bottom: 20px;
}

#overlay .bezel span:nth-of-type(2) {
  font-size: 12px;
  font-weight: 200;
}
